      <v-col cols="12">
        <v-card class="mx-auto" raised>
          <v-list-item dense>
            <v-list-item-content>
              <v-row dense>
                <!-- no-gutters -->
                <!-- <v-col cols="4">
        <v-list-item-title class="headline mb-1">Headline 5</v-list-item-title>
                </v-col>-->
                <v-col cols="8">
                  <v-text-field label="餘額" filled dense hide-details v-model="remainMoney"></v-text-field>
                </v-col>
                <v-col cols="4">
                  <v-btn style="height:50px; width:100px" color="primary" hide-details>計算</v-btn>
                </v-col>
                <v-col cols="12">
                  <!--<v-text-field label="提示" filled v-model="hints"></v-text-field> -->
                  提示： 使用剩下日數：<span class="color:red">{{useDays}}</span>，剩下金額：{{remainMoney}}
                  ，每日最少使用金額：{{ (remainMoney / useDays).toFixed(2) }}
                  ，最少需要多少日才使用完(上限每日300)：{{ Math.ceil(remainMoney/300) }}
                </v-col>
                <v-col cols="12" v-if="standbyPeriod"> 
                  使用開始日倒數計時：
                </v-col>
                <v-col cols="12" v-else> 
                  使用完結日倒數計時：
                </v-col>                
                <v-col cols="12" v-if="standbyPeriod">
                  <!-- <flip-countdown deadline="2020-03-19 00:00:00"></flip-countdown> -->
                  <flip-countdown :deadline="useStartDate.toISOString().substring(0, 10)"></flip-countdown>
                </v-col>
                <v-col cols="12" v-else>
                  <flip-countdown :deadline="useEndDate.toISOString().substring(0, 10)"></flip-countdown>
                </v-col>                
              </v-row>
            </v-list-item-content>
          </v-list-item>
          <!-- 
    <v-card-actions>

          </v-card-actions>-->
        </v-card>
      </v-col>
    </v-row>
    <v-app id="inspire">
      <v-timeline align-top :dense="$vuetify.breakpoint.smAndDown">
        <v-timeline-item key="0" 
          :color="items[0].color"
          :icon="items[0].icon"
          fill-dot
        >
          <v-card :color="items[0].color" dark>
            <v-card-title class="title">登記期 - 3月18日 至 4月8日</v-card-title>
            <v-card-text class="white text--primary">
              <p>
                

              </p>
              <!-- <v-btn :color="items[0].color" class="mx-0" outlined>Button</v-btn> -->
            </v-card-text>
          </v-card>
        </v-timeline-item>

        <v-timeline-item key="1" 
          :color="items[1].color"
          :icon="items[1].icon"
          fill-dot
        >
          <v-card :color="items[1].color" dark>
            <v-card-title class="title">領取期 - 4月14日 至 4月30日</v-card-title>
