              <p>
              <a href="https://www.amcm.gov.mo/static/consumptionvoucher/bankbranch.html">電子消費卡領取地點</a>              
              <br/>
              <a href="https://www.amcm.gov.mo/static/consumptionvoucher/doc/kinshipdeclaration.pdf">親屬關係及監護人聲明書</a>
               <br/>
              <a href="https://www.amcm.gov.mo/static/consumptionvoucher/doc/authorization.pdf">授權書</a>
              </p>              
              
