          </v-card>
        </v-timeline-item>

        <v-timeline-item key="2" 
          :color="items[2].color"
          :icon="items[2].icon"
          fill-dot
        >
          <v-card :color="items[2].color" dark>
            <v-card-title class="title">使用期 - 5月1日起 至 7月31日</v-card-title>
            <v-card-text class="white text--primary">
              <p></p>
            </v-card-text>
          </v-card>
        </v-timeline-item>                
        
        <!-- <v-timeline-item
          v-for="(item, i) in items"
          :key="i"
          :color="item.color"
          :icon="item.icon"
          fill-dot
        >
          <v-card :color="item.color" dark>
