      </v-timeline>
    </v-app>
  </v-container>
</template>

<script>
import FlipCountdown from 'vue2-flip-countdown'

export default {
  name: "TimeLine",
  components: { FlipCountdown },
  data: () => ({
    remainMoney : 3000,
    useStartDate: new Date("2020-05-01"),
    useEndDate: new Date("2020-08-01"),
    hints: '平均每日最少消費300元',
    dates:[
      new Date("2020-03-18"),
      new Date("2020-04-08"),
      new Date("2020-04-14"),
      new Date("2020-04-30"),
      new Date("2020-05-01"),
      new Date("2020-07-31"),
    ],
    items: [
      {
        color: "red lighten-2",
        icon: "mdi-lead-pencil"
      },
      {
        color: "purple darken-1",
        icon: "mdi-credit-card"
      },
      {
        color: "green lighten-1",
        icon: "mdi-cash-usd"
      },
      {
        color: "indigo",
        icon: "mdi-buffer"
      }
    ]
  }),

  computed: {
    // a computed getter
    useDays: function () {
      var Difference_In_Time = this.useEndDate.getTime() - this.useStartDate.getTime(); 
      var Difference_In_Days = Difference_In_Time / (1000 * 3600 * 24); 
      return Difference_In_Days;
    },
    standbyPeriod: function() {
      var now = new Date();
      now.setHours(0,0,0,0);     
      if (now < this.dates[4] )
        return true;
      else
        return false;
    },
    nextCountDownDate: function () {
      var now = new Date();
      now.setHours(0,0,0,0);
      if (now >= this.dates[5])
        return now;
      else if (now >= this.dates[4])
        return this.dates[5]
      else if (now >= this.dates[3])
        return this.dates[4]
      else if (now >= this.dates[2])
        return this.dates[3]
      else if (now >= this.dates[1])
        return this.dates[2]
      else if (now >= this.dates[0])
        return this.dates[1]
      else
        return this.dates[0];
    },    
  }
};
</script>

